@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500&display=swap');

* {
  box-sizing: border-box;
}

html {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-size: 18px;
  color: #000;
  position: relative;
  background-color: #eee;
}

a {
  color: inherit;
}

html, body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

#bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
}

.project-description h1 {
  font-size: 1rem;
  font-weight: inherit;
}